.color1 { color: #cea040 }
.color2 { color: #dec68f }
.color3 { color: #573d21 }
.color4 { color: #609e98 }
.color5 { color: #accbd3 }

.container {
  background-color: #dec68f;
  background-image: url("./img/landing.jpeg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
.btn {
  position: absolute;
  font-size: 24px;
  border-radius: 15px;
  padding: 32px 16px;
  box-shadow: 0 8px 16px 0 rgba(244, 241, 241, 0.847),
    0 6px 20px 0 rgba(228, 221, 221, 0.667);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #dec68f;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.5s;
  box-shadow: 10px 5px 5px black;
}

.btn span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.btn span:after {
  content: "\00bb";
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.btn:hover span {
  padding-right: 25px;
}

.btn:hover span:after {
  opacity: 1;
  right: 0;
}
