.color1 { color: #cea040 }
.color2 { color: #dec68f }
.color3 { color: #573d21 }
.color4 { color: #609e98 }
.color5 { color: #accbd3 }

.about{
  display: flex;
  position: fixed;
  background-color: #8addf1;
  height: calc(100vw);
  width: calc(100vw);
}

.about img {
  height: calc(100vw - 80vw);
  width: calc(100vw - 80vw);
  border-radius: 50%;
  margin: 5rem;
  padding-top: 5rem;
  margin-bottom: 0rem;
}

.container {
  padding-top: 1rem;
  width: calc(100vw - 50vw);
}

.container p {
  margin: 0;
  padding-left: 2rem;
  word-wrap: break-word;
  font-size:calc(100vw - 98.9vw);
}

.container li{
  font-size:calc(100vw - 98.9vw);
  
}

.my{
  text-align: center;
  width: calc(100vw - 60vw);
}

h3{
  margin: .2rem;
}

.email{
  color: blue;
  cursor: pointer;
  font-size:calc(100vw - 98vw);
}
.email:hover{
  color: aqua;
}

.a{
  font-size:calc(100vw - 98vw);
}

.span{
  color: #cea040;
}